import React from 'react';
import { motion, AnimatePresence } from 'framer-motion';
import { ReactComponent as Cross } from '../../images/cross.svg';
import { Link } from 'react-router-dom'
import { HashLink } from 'react-router-hash-link';

const sliderVariant = {
    initial: {
        x: '100vw'
    },
    final: {
        x: 0,
        transition: {
            type: 'tween',
            duration: 0.5,
            ease: 'easeInOut'
        }
    },
    exit: {
        x: "500vw",
        duration: 2,
    }
};

const sliderContainerVariant = {
    initial: {
        backgroundColor: "rgba(0,0,0,0)"
    },
    final: {
        backgroundColor: "rgba(0,0,0,0.4)",
        transition: {
            delay: 0.3
        }
    },
    exit: {
        backgroundColor: "rgba(0,0,0,0)",
        duration: 2,
        delay: 1
    }
};

const Slider = ({ displayNone, reveal }) => {
    return (
        <AnimatePresence>
            { reveal && <motion.div
                className='slider'
                style={sliderContainerStyle}
                onClick={e => displayNone(e)}
                variants={sliderContainerVariant}
                initial="initial"
                animate="final"
                exit='exit'
            >
                <AnimatePresence>
                    {reveal &&
                        (<motion.div
                            style={sliderStyle}
                            variants={sliderVariant}
                            exit='exit'
                        >
                            <button
                                style={crossStyling}
                                className='cancelSlider'
                                onClick={e => displayNone(e)}
                            >
                                <Cross className='cancelSlider' />
                            </button>
                            <Link onClick={e => displayNone(e)} to='/'>Home</Link>
                            <HashLink onClick={e => displayNone(e)} to='/#about'>About</HashLink>
                            <HashLink onClick={e => displayNone(e)} to='/#products'>Products</HashLink>
                            <HashLink onClick={e => displayNone(e)} to='/#services'>Services</HashLink>
                            <HashLink onClick={e => displayNone(e)} to='/#departments'>Departments</HashLink>
                            <HashLink onClick={e => displayNone(e)} to='/#values'>Values</HashLink>
                            {/* <HashLink onClick={e => displayNone(e)} to='/#team'>Team</HashLink> */}
                            <HashLink onClick={e => displayNone(e)} to='/#contact'>Contact</HashLink>
                        </motion.div>)
                    }
                </AnimatePresence>
            </motion.div>
            }</AnimatePresence>
    )
}

const sliderStyle = {
    background: '#fff',
    height: "100%",
    float: "right",
};

const sliderContainerStyle = {
    position: "fixed",
    height: "100vh",
    zIndex: "100000",
    width: "100%",
};

const crossStyling = {
    float: 'right',
    margin: "30px",
    background: "#fff",
    border: 'none',
    outline: 'none'
};

export default Slider;
