import React, { useState, useEffect, Fragment, useRef } from 'react';
import Slider from './Slider';
import { motion } from 'framer-motion';
import { routes } from '../../utils/exportContent';

const headerVariant = {
    initial: {
        y: -100
    },
    final :{
        y: 0,
        transition: {
            type: 'tween',
            duration: 0.5
        }
    }
};

 
const Header = () => {

    const headerRef = useRef();
    const [headershrink, setHeadershrink] = useState(true);
    const [scrollWidth, setScrollWidth] =   useState(0);
    const [isOpen, setIsOpen] = useState(false);
    const [reveal, setReveal] = useState(false);
    let location = useRef(window.location.pathname);

    if (location.current !== window.location.pathname) {
        location.current = window.location.pathname;
        setScrollWidth(0);
        document.querySelector('.progress').style.width = '0';
    }

    const headerStyle = {
        width: "80%",
        margin: "0 auto",
        padding: headershrink ? "15px 0" : "20px 0",
        transition: "padding 0.5s ease"
    };
    
    const progress = {
        width: `${scrollWidth}%`,
        height: "2px",
        background: "black",
        transition: "width 0.2s ease"
    };

    useEffect(() => {
        function headerContainerChange() {
            let scrollTop = document.documentElement.scrollTop;
            const screenHeight = window.innerHeight;
           
            // setting the header to shrink on scrolling down
            if (scrollTop > 80) 
                setHeadershrink(true);
            else 
                setHeadershrink(false);
            
            if (screenHeight <= scrollTop || routes.includes(location.current)) {
                headerRef.current.style.background = '#fff';
                headerRef.current.style.borderBottom = '1px solid #f1f1f1'; 
            }
            else {
                headerRef.current.style.background = 'rgba(255, 255, 255, 0)';
                headerRef.current.style.borderBottom = 'none';
            }
            
            // scollable height
            const scrollable = document.documentElement.scrollHeight - window.innerHeight;
            setScrollWidth((scrollTop / scrollable)*100);
        }

        window.addEventListener('scroll', headerContainerChange);
        window.addEventListener('load', headerContainerChange);

        return () => {
        window.addEventListener('scroll', headerContainerChange);
        window.addEventListener('load', headerContainerChange);
        };
    }, [scrollWidth, reveal]);

    useEffect(() => {
        isOpen ? 
            document.body.style.overflowY = 'hidden' : 
            document.body.style.overflowY = 'auto'
    }, [isOpen])

    const revealNavbar = () => {
        setReveal(prevState => prevState = !prevState);
        setIsOpen(true)
    }

    const displayNone = e => {
        if (e.target.classList.contains('slider') || e.target.classList.contains('cancelSlider') || e.target.nodeName === 'A') {
            setReveal(false);
            setIsOpen(false)
        }
    }

    // const removeClass = type => {
    //     const cursor = document.querySelector('.cursor')
    //     type === 'remove' ? 
    //         cursor.classList.remove('cursor-hover-ham') : 
    //         cursor.classList.add('cursor-hover-ham') 
    // }

    return (
        <Fragment>
            <div className='header-main-container' ref={headerRef}>
                <div className='progress' style={progress}></div>
                <motion.div 
                    style={headerStyle}
                    variants={headerVariant}
                    initial='initial'
                    animate='final'
                >   
                    <a href='/'>
                        <img 
                            width={headershrink ? "20" : "30"} 
                            src={require('../../images/logo.png')} 
                            alt='logo' 
                        />
                    </a>
                    <svg 
                        // onMouseOver={() => removeClass('add')}
                        // onMouseLeave={() => removeClass('remove')}
                        onClick={revealNavbar} 
                        style={hamburger} 
                        width="70" 
                        height={headershrink ? "15" : "20"} 
                        viewBox="0 0 100 40" 
                        fill="none" xmlns="http://www.w3.org/2000/svg"
                    >
                        <rect width="40" height="4" fill="black"/>
                        <rect y="13" width="40" height="4" fill="black"/>
                    </svg>
                </motion.div>
            </div>

            <Slider displayNone={displayNone} reveal={reveal} />
        </Fragment>
    )
}

const hamburger = { 
    float: "right", 
    cursor: "pointer", 
    marginTop: "7px",
    transition: "transfrom 0.5s ease",
}

export default Header
